html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

html, body {
  font-family: "Lato", sans-serif;
  font-size: 16px;
  background: #F7F4ED;
  color: #2D5A79; }

.no-scroll {
  overflow-y: hidden; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Zilla Slab", serif; }

