  text-decoration: none;
  transition: all 0.2s cubic-bezier(0.47, 0, 0.745, 0.715); }
  a:hover {
    color: #FF7B54; }

p {
  line-height: 1.5;
  padding: 12px 0; }

section {
  padding: 96px 0;
  position: relative; }
  section h2 {
    font-size: 32px;
    margin: 0 0 12px; }
  section h3 {
    font-size: 24px;
    margin: 0 0 16px; }

.container {
  max-width: 1200px;
  margin: 0 auto; }

.btn {
  display: inline-block;
  padding: 11px 12px;
  border-radius: 3px;
  transition: all 0.2s cubic-bezier(0.47, 0, 0.745, 0.715); }
  .btn.raised {
    box-shadow: 2px 2px 1px rgba(0, 0, 0, 0.1);
    font-family: "Zilla Slab", serif; }
    .btn.raised.primary {
      background-color: #FF7B54;
      text-transform: uppercase;
      letter-spacing: 1px;
      font-weight: 700;
      color: #F7F4ED; }
      .btn.raised.primary:hover {
        background: #2D5A79;
        color: #F7F4ED; }
  .btn.blank:hover, .btn.secondary:hover, .btn.tertiary:hover {
    background: rgba(0, 0, 0, 0.05);
    color: inherit; }
  .btn.w-icon {
    text-transform: uppercase;
    letter-spacing: 2px; }
  .btn.tertiary {
    color: #FF7B54; }
  .btn.icon {
    background: none;
    border: 0; }
    .btn.icon:hover {
      cursor: pointer; }

.section-actions {
  position: absolute;
  bottom: 96px;
  left: 0;
  right: 0; }

.cover {
